/* Navigation */
.logo-btn {
    min-width: 5rem;
}

.logo-btn * {
    pointer-events: none;
    color: var(--clr-grey);
}

.nav-btn:hover {
   opacity: 0.8;
}

.nav-btn a {
    color: var(--clr-grey);
}

.nav-btn svg {
    display: block;
}

.auth-btn,
.sigin-btn {
    display: none;
}

.auth-btn:hover {
    opacity: 0.8;
}

/* .auth-btn a {
    display: block;
    color: var(--clr-grey);
} */

.auth-btn a * {
    pointer-events: none;
}

.side-panel {
    /* display: none; */
    position: absolute;
    top: 6rem;
    right: 0;
    width: 0;
    min-height: calc(100vh - var(--header-height-wide)); 
    background-color: var(--clr-light);
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    transition: width 0.2s ease-in-out;
    z-index: 1000;
    box-shadow: 0px 0px 18px 0px rgba(0,0,0,0.2);
}

.side-show {
    /* display: block; */
    width: 30rem;
}

.auth-modal ul {
    height: inherit;
    display: block;
    position: static;

}

.auth-modal a {
   display: block;
   padding: 1rem;
   text-align: center;
   font-size: var(--fs-150);
   color: var(--clr-grey);
   background-color: var(--clr-primary);
   border-top: 1px solid var(--clr-grey);
}

/* .auth-modal a:hover { 
    background-color: var(--clr-light-grey);
    color: var(--clr-primary);
} */


.signin-nav-btn {
    display: none;
}

.auth-btn,
.hamburger-btn,
.search-btn {
    cursor: pointer;
}

.auth-btn *,
.hamburger-btn *,
.cart-btn *,
.mail-btn *,
.search-btn *
 {
    pointer-events: none;
}

/* Avatar */
.avatar-btn {
    display: none;
    min-width: 3.3rem;
    min-height: 3.3rem;
    border-radius: 50%;
    font-size: var(--fs-200);
    color: var(--clr-grey);
}



nav .nav-btns {
    display: flex;
    width: 100%;
    align-items: center;
    gap: 1.5rem;
}

nav .nav-links {
    height: 100%;
    display: flex;
}


nav .nav-links ul {
    position: fixed;
    inset: 5rem 0 0 100%;
    display: flex;
    flex-direction: column;
    min-height: calc(100vh - var(--header-height));
    overflow-y: hidden;
    transition: 0.2s ease;
    /* border-top: 1px solid var(--clr-grey); */
    background-color: var(--clr-light);
    z-index: 9000;
}

.nav-link {
    display:block;
    padding: 1.5rem;
    width: 100%;
    font-size: var(--fs-175);
    color: var(--clr-dark);
    border-bottom: 1px solid var(--clr-grey);
    z-index: 9000;
}

.logout:hover {
    cursor: pointer;
}

/* Hamburger */
#mobile-nav {
    display: none;
}

.hamburger {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 3rem;
    min-height: 4rem;
}

.mobile-nav-label {
    position: relative;
    display: flex;
    align-items: center;
    min-width: var(--size-3);
    min-height: 2em;
    cursor: pointer;
}
.mobile-nav-label span,
.mobile-nav-label::before,
.mobile-nav-label::after {
    width: var(--size-3);
    height: 3px;
    border-radius: 5px;
    background-color: var(--clr-grey);
    transition: 0.2s ease;
    z-index: 999;
}

.mobile-nav-label::before,
.mobile-nav-label::after {
    content: '';
    position: absolute;
}

.mobile-nav-label::before {
    top: 2px;
}

.mobile-nav-label::after {
    bottom: 2px;
}

/* Hamburger animation */
/* 'checked' className option for Firebox browser added to header @ main.js */
header:has(input:checked) .mobile-nav-label::before,
header.checked .mobile-nav-label::before {
    rotate: -45deg;
    translate: 1px 9px;
}


header:has(input:checked) .mobile-nav-label::after,
header.checked .mobile-nav-label::after {
    rotate: 45deg;
    translate: 1px -9px;
}

header:has(input:checked) .mobile-nav-label span,
header.checked .mobile-nav-label span {
    opacity: 0;
}

