/* Logo styles */
.section--logo {
  display: flex;
  justify-content: flex-start;
  width: 65%;
  align-items: center;
  padding-left: 5em;
  font-size: 0.8rem;
  color: #fff;
}

.section-mobile-icons {
  width: 35%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.padding-right {
  padding-right: 1em;
}

.section--search {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color: #fff;
}


.searchbar {
  position: absolute;
  /* display: none; */
  background-color: var(--background);
  width: 100%;
  top: 100%;
  left: 0;
  padding: 0.2em 0.1em;
  transform: scale(1, 0);
  /* transform: translate(0, 100px); */
  transform-origin: top;
  transition: transform 120ms ease-in-out;
}



.signout {
  color: #fff;

  padding: 0.4em 0;
  padding-left: 1em;
  /* text-align: center;  */
}

.signout:hover {
  cursor: pointer;
  opacity: 0.8;
}

/* Search mobile */
.search-toggle {
  display: none;
}

.search-toggle:checked ~ .searchbar {
  /* display: block; */
  transform: scale(1, 1);
}

.search-toggle-label {
  display: flex;
  align-items: center;
}



/* Mail icon container */

/* Icons */
.section--cart {
  position: relative;
}

.section--mail a svg,
.section--cart a svg {
  display: block;
  color: #fff;
}

/* Cart icon color */
.section--cart a svg {
  color: #fff;
}

.login,
.register {
  width: 125px;
}
.login a,
.register a {
  color: var(--light-color);
  padding: 8px 0;
}

.login a {
  background: var(--color-success);
}

.register a {
  background: var(--primary-color);
}

button.register,
button.submit,
button.warning,
button.danger,
button.secondary {
  color: var(--extra-light-color);
  /* padding: 0.8em 0; */
  /* margin-top: 1em; */
  /* font-size: 1rem; */
  border: none;
}

button.submit.message,
button.register.cart {
  font-size: 0.9rem;
  padding: 15px 0;
}

button.primary {
  background: var(--primary-color);
}

button.success {
  background: var(--color-success);
}

button.warning {
  background: var(--warning-color);
}
button.danger {
  background: var(--danger-color);
}

button.secondary {
  background: var(--secondary-color);
}

/* button:hover, */
div.section--mail:hover,
div.section--cart:hover {
  cursor: pointer;
  opacity: 0.8;
}



/* ********************* */
/* Loged in Dropdown menu */
/* ********************* */
div.auth-dropdown {
  position: relative;
  display: inline-block;
  min-width: 220px;
  color: #fff;
  border-bottom: 1px solid var(--light-color);
}

div.auth-name {
  display: flex;
  justify-content: space-between;
  padding: 10px;
}

ul.auth-dropdown-content {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 46px;
  right: 0;
  min-width: 100%;
  background: #fff;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
  z-index: 10;
}

ul.auth-dropdown-content li {
  width: 100%;
  display: block;
  margin: 1px 0;
}

ul.auth-dropdown-content li a {
  display: block;
  width: 100%;
  padding: 0.5em;
  color: var(--dark-color);
  border-bottom: 1px solid var(--light-color);
}

ul.auth-dropdown-content li .signout {
  padding: 0;
  color: var(--dark-color);
  padding: 0.5em;
}

.signout:hover {
  cursor: pointer;
  opacity: 0.8;
}






