.search-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin: 1em 0;
  padding: 0.5em;
  font-size: 0.9rem;
  border-bottom: 1px solid #e4e4e4;
  box-shadow: 0px 5px 5px -5px #e4e4e4;
}

.clear-search {
  display: flex;
  align-items: center;
  color: #333;
}

.clear-search:hover {
  cursor: pointer;
  opacity: 0.8;
}

.clear-search svg {
  margin-left: 0.5em;
  width: 1.5em;
  height: 1.5em;
}

/* User main page */
.user-page-container {
  display: flex;
  flex-direction: column;
}

.user-page-container a {
  display: block;
}

.user-page-section {
  display: flex;
  flex-direction: column;
  min-height: 20vh;
  width: 100%;
  margin-bottom: 1em;
  border: 1px solid #e4e4e4;
  border-radius: 0.5em;
}

.user-page-section h2 {
  text-align: center;
  margin: 0;
  padding: 0.5em 0;
}

.user-page-section article {
  font-size: 0.9rem;
  padding: 1em;
}

.user-page-section article p {
  font-size: 0.9rem;
  /* padding: 0 1em; */
}





#item-order {
  /* @ views/CatalogItem.js */
  display: block;
  font-size: 0.9rem;
  width: 20%;
  background: #fff;
  color: #333;
  text-align: center;
  /* padding: 0.5em 0; */
  border: 1px solid #e4e4e4;
}

.item-order:focus {
  outline: none;
}

.catalog-cart-button {
