/* Mail styles */
.mailbox-container {
  /* @ views/mail/MailBox.js */
  display: flex;
  flex-direction: column;

  /* font-size: 0.8rem; */
}

.mailbox-content {
  width: 100%;
  margin-top: 1em;
  border: 1px solid #f5f8fa;
}

.mailbox-aside {
  /* @views/mail/Inbox.js  */
  /*  width: 20% @ widescreen */
  width: 100%;
}

.mailbox-header {
  /* @ views/mail/MailHeader.js */
  position: relative;
  display: flex;
  width: 100%;
  padding: 2.5em 0;
  font-size: 0.6rem;
  color: #fff;
  background: #636363;
}

.mailbox-row {
  display: flex;
  /* width: 33%; */
  /* justify-content: flex-end; */
}

.mail-row {
  position: relative;
  display: flex;
}

.mailbox-section-1 {
  position: relative;
  display: flex;
  align-items: center;
  width: 40%;
  /* padding-left: 1em; */
}

.mailbox-section-2 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 60%;
  padding-right: 1em;
}
.mailbox-header > .mail-checkbox {
  top: 1.6em;
}

.mail-checkbox {
  position: absolute;
  top: 1.7em;
  left: 1.2em;
  z-index: 20;
  display: block;
  width: 1.5em;
  height: 1.5em;
}

.mail-message {
  display: flex;
  align-items: center;
  width: 100%;
  font-size: 0.9rem;
  padding: 1.5em 0;
  background-color: #fff;
  border-bottom: 1px solid #aabbc2;
}

.mail-message:hover {
  cursor: pointer;
  border-left-color: #aabbc2;
}

.mail-checkbox:hover {
  cursor: pointer;
}

.row-to {
  padding-left: 3.5em;
}
.row-from {
  /* @ views/mail/MailHeader.js */
  padding-left: 3.5em;
}

.row-from {
}

.row-subject {
  /* @ views/mail/MailHeader.js */
