}
.row-sent {
  /* @ views/mail/MailHeader.js */
  width: 30%;
}

.mailbox-sidebar-container {
  width: 100%;
  border: 1px solid #f5f8fa;
}

.mailbox-sidebar {
  display: flex;
  flex-direction: column;
}

.mailbox-sidebar-header {
  /* height: 12vh; */
  display: flex;
  justify-content: space-between;
  margin-bottom: 2em;
  width: 100%;
}

.mailbox-sidebar-options {
  display: flex;
  width: 100%;
}

.mailbox-sidebar-options li {
  width: 25%;
}

.mailbox-sidebar-options li a {
  text-align: center;
  font-size: 0.9rem;
  padding: 0.8em 0;
}

button.remove-message {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48%;
  font-size: 0.9rem;
  padding: 0.7em 0;
  background: #dc3545;
  color: #fff;
  border: 2px solid #dc3545;
}

button.compose-message {
  font-size: 0.9rem;
  width: 48%;
  padding: 0.7em 0;
  background: #28a745;
  color: #fff;
  border: 2px solid #28a745;
}

.message-container {
  /* @ views/mail/Message */
}

.message-details {
  /* @ views/mail/Message */
  padding-left: 1em;
  padding-bottom: 1em;
  border-bottom: 1px solid #e1e8ed;
}

.mail-content {
  /* @ views/mail/Message */
  min-height: 20vh;
  font-size: 1rem;
  border-bottom: 1px solid #e1e8ed;
  padding: 1em;
  word-wrap: break-word;
}


